// header
.header-block {
    background: #AA0601;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 30px 88px;
    .menu {
        display: flex;
        flex-wrap: wrap;
        a {
            color: white;
            font-size: 20px;
            padding: 0px 32px;
            &:hover {
                color: green;
            }
        }
    }
    @include pad {
        padding: 30px 39px;
        .menu {
            a {
                padding: 0 20px;
            }
        }
    }
